.navigationbar {
    background: #2E6CA4;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
}
  
.navigationbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
}

.navigationbar-logo img {
    width: auto;
    height: 100%;
    margin-left: 5em;
}
  
.navigation-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
    margin-bottom: 0;
}

.navigation-item {
    height: 80px;
}

.navigation-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
}
  
.navigation-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
    text-decoration: none;
    color: #fff;
}

.fa-bars {
    color: #fff;
}

.navigation-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {

    .navigation-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 100px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .navigation-menu.active {
        background: #2E6CA4;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        }
    
    .navigation-links {
        text-align: center;
        padding: 2rem 2rem 0 0;
        width: 100%;
        display: table;
    }

    .navigation-links:hover {
        background-color: #fff;
        color: #242424;
        border-radius: 0;
    }

    .navigationbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        /*transform: translate(5em, 0%);*/
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 100%);
        font-size: 2rem;
        cursor: pointer;
    }

    .fa-times {
        color: #fff;
        font-size: 2rem;
    }
    
    .navigation-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: #fff;
        padding: 14px 20px;
        border: 1px solid #fff;
        transition: all 0.3s ease-out;
    }
    
    .navigation-links-mobile:hover {
        background: #fff;
        color: #242424;
        transition: 250ms;
    }
}